import React, { useState, useEffect } from 'react';
import Loader from './Loader';
import star from "../Icons/star.svg";

let UrlPelicula= `https://api.themoviedb.org/3/movie/popular?api_key=30489992cf208d4c62469ce690c01f88&language=es&page=`, 
    RutaImg = "https://image.tmdb.org/t/p/original",
PageActually = 1;



function Noticia(props) {
    let {name, image, Puntuacion } = props;

    const { handleArticul } = props;
    return (
        <figure>
            <img src={image} alt={name} />
            <figcaption>{name}</figcaption>
            <button onClick={() => { handleArticul(props) }}>Ver Mas</button>
            <div><img src={star} alt="Puntuación"></img><h6>{Puntuacion}</h6></div>
        </figure>
    )
}





export default function MoviesPopulares(props) {
    const [Noticias, setNoticias] = useState([]);
    const { handleDataArticul } = props;
    
    
    const getNoticias = async (url, page) => {
        window.scroll(0, 0);
        page = page < 1 ? 1 : page;

        let UrlCompleta = `${url}${page}`,
            res = await fetch(UrlCompleta),
            json = await res.json();
            PageActually = json.page;

            setNoticias([]);
                json.results.forEach(async (el) => {//segunda peticion para obtener imagen
                    
                    let Noticia = {
                        id: el.id,
                        name: el.title,
                        img: `${RutaImg}${el.poster_path}`,
                        poster: `${RutaImg}${el.backdrop_path}`,
                        description: el.overview,
                        Puntuacion: el.vote_average,
                    }

                    
                    
                    setNoticias((noticias) => [...noticias, Noticia]);
                });
                
        }

    useEffect(() => {
        getNoticias(UrlPelicula,PageActually);
    }, []);




    /* Para comunicar App, con su nieto se le pasa en handle al padre y el padre al hijo,
    al hacer el cambio en el hijo, se le envia al padre y en este se ejecuta esta funcion que
    a cada cambio llama a la funcion de cambio del abuelo y actualiza su estado*/
    const handleArticul =  async (props) => {
        await handleDataArticul(props);

        let NuevaRuta= `#Movie=${props.id}`;
        window.location.hash = NuevaRuta;
        window.scroll(0, 0);
    }
    /* ------------------------------------------------------------- */

    /* Atributos de scroll */
    return (
        <>
            <h2 className="TitleArticuls">POPULARES</h2>
            
            {Noticias.length <=19 ? <Loader></Loader> :
                <div className="ContainerNews">
                    {Noticias.map((el) => (
                        <Noticia
                            key={el.id}
                            id={el.id}
                            name={el.name}
                            image={el.img}
                            description={el.description}
                            Puntuacion={el.Puntuacion}
                            Poster={el.poster}
                            handleArticul={handleArticul}></Noticia>))}
                </div>}
            
            <div className="DivButtonsPage">
                {PageActually === 1
                    ?<button className="ButonActivado" onClick={() => { getNoticias(UrlPelicula, 1) }}>1</button>
                    : <button onClick={() => { getNoticias(UrlPelicula, 1) }}>1</button>
                }
                {PageActually === 2
                    ?<button className="ButonActivado" onClick={() => { getNoticias(UrlPelicula, 2) }}>2</button>
                    : <button onClick={() => { getNoticias(UrlPelicula, 2) }}>2</button>
                }

                {PageActually === 3
                    ?<button className="ButonActivado" onClick={() => { getNoticias(UrlPelicula, 3) }}>3</button>
                    : <button onClick={() => { getNoticias(UrlPelicula, 3) }}>3</button>
                }

                {PageActually === 4
                    ?<button className="ButonActivado" onClick={() => { getNoticias(UrlPelicula, 4) }}>4</button>
                    : <button onClick={() => { getNoticias(UrlPelicula, 4) }}>4</button>
                }

                {PageActually === 5
                    ?<button className="ButonActivado" onClick={() => { getNoticias(UrlPelicula, 5) }}>5</button>
                    : <button onClick={() => { getNoticias(UrlPelicula, 5) }}>5</button>
                }

                {PageActually === 6
                    ?<button className="ButonActivado" onClick={() => { getNoticias(UrlPelicula, 6) }}>6</button>
                    : <button onClick={() => { getNoticias(UrlPelicula, 6) }}>6</button>
                }

                {PageActually === 7
                    ?<button className="ButonActivado" onClick={() => { getNoticias(UrlPelicula, 7) }}>7</button>
                    : <button onClick={() => { getNoticias(UrlPelicula, 7) }}>7</button>
                }

                {PageActually === 8
                    ?<button className="ButonActivado" onClick={() => { getNoticias(UrlPelicula, 8) }}>8</button>
                    : <button onClick={() => { getNoticias(UrlPelicula, 8) }}>8</button>
                }

                {PageActually === 9
                    ?<button className="ButonActivado" onClick={() => { getNoticias(UrlPelicula, 9) }}>9</button>
                    : <button onClick={() => { getNoticias(UrlPelicula, 9) }}>9</button>
                }

                {PageActually === 10
                    ?<button className="ButonActivado" onClick={() => { getNoticias(UrlPelicula, 10) }}>10</button>
                    : <button onClick={() => { getNoticias(UrlPelicula, 10) }}>10</button>
                }
            </div>
            
        </>
    );
    
}


