import React, { useState, useEffect, useRef } from 'react';
import MoviesPopulares from "./MoviesPopulares";
import LeerArticulo from './LeerArticulo';
import Action from "../Icons/Action.svg";
import like from "../Icons/like.svg";
import rocking from "../Icons/rocking.svg";
import theater from "../Icons/theater.svg";
import ufo from "../Icons/ufo.svg";
import Footer from './Footer';
import MoviesAction from './MoviesAction';
import MoviesAnimation from './MoviesAnimation';
import MoviesComedy from './MoviesComedy';
import MoviesCF from './MoviesCF';
import Menu from "../Icons/hamburger.svg";

/* let NuevaRuta = ""; */

function Aplication() {

    const [ReadArticul, setReadArticul] = useState({});
    const [location, setLocation] = useState(window.location.hash);
    let refMenuBtn = useRef(),
        refMenuNav = useRef(),
        MenuVisible = false;
    /* const [busqueda, setBusqueda] = useState(""); */
    /* const [nuevaBusqueda, setNuevaBusqueda] = useState(false); */

    useEffect(() => {

        const DetectarScroll = () => {
            let hash = window.location;
            setLocation(hash.hash);
        }
        
        //Asignamos la funcion para que se ejecute cada
        //vez que reciba el evento scroll
        window.addEventListener("hashchange", DetectarScroll);

        //Cuando el elemento ya no exista nos desubcribimos del evento
        return () => {
            window.removeEventListener("hashchange", DetectarScroll);
            //console.log("Fase de desmontaje");
        }

    }, [location]);


    const handleDataArticul = (props) => {
        setReadArticul(props);
    }

    const handleMenu = (e) => {

        if (!MenuVisible) {
            refMenuBtn.current.style.position = "fixed";
            refMenuNav.current.style.visibility = "visible";
            refMenuNav.current.style.display = "flex";
            refMenuNav.current.style.opacity = "1";
            MenuVisible = true;
        } else if (MenuVisible) {
            refMenuBtn.current.style.position = "absolute";
            refMenuNav.current.style.visibility = "hidden";
            refMenuNav.current.style.display = "none";
            refMenuNav.current.style.opacity = "0";
            MenuVisible = false;
        }
        
    }

    const handleOcultarNav = (e) => {
        if (window.innerWidth<=720) {
            refMenuBtn.current.style.position = "absolute";
            refMenuNav.current.style.visibility = "hidden";
            refMenuNav.current.style.display = "none";
            refMenuNav.current.style.opacity = "0";
            MenuVisible = false;
        }
    }

    /* const handleSubmit = (e) => {
        e.preventDefault();
        let Busqueda = busqueda.trim();
        if (Busqueda.length===0) {
            alert("No has ingresado nada");
        } else {
            let BusquedaSinEspacio = busqueda.replace(/\s+/g, '+');
                NuevaRuta = `#Search=${BusquedaSinEspacio}`;
            console.log(BusquedaSinEspacio);
            window.location.hash = NuevaRuta;
            window.scroll(0, 0);
            setNuevaBusqueda(true);
            console.log(nuevaBusqueda);
            
        } 
    }*/

return (
    <div className="App">
    <header className="header">
            <h1>DatMovies!</h1>
            <img ref={refMenuBtn} src={Menu} alt="Menu Icon" className="IconMenu" onClick={handleMenu}></img>
            <nav ref={refMenuNav} >
                <a onClick={handleOcultarNav} href="#Populares"><img src={like} alt="Top Movies"></img> POPULARES</a>
                <a onClick={handleOcultarNav} href="#Animation"><img src={rocking} alt="Animation"></img>ANIMACIÓN</a>
                <a onClick={handleOcultarNav} href="#Comedy"><img src={theater} alt="Comedy"></img> COMEDIA</a>
                <a onClick={handleOcultarNav} href="#ScienceFiction"><img src={ufo} alt="Science-Fiction "></img>CIENCIA FICCIÓN</a>
                <a onClick={handleOcultarNav} href="#Action"><img src={Action} alt="Action"></img> ACCIÓN</a>
                
            </nav>

    </header>
        {location === "" ? <MoviesPopulares handleDataArticul={handleDataArticul}></MoviesPopulares> : false}
        {location === "#Populares" ? <MoviesPopulares handleDataArticul={handleDataArticul}></MoviesPopulares> : false}
        {location === "#Action" ? <MoviesAction handleDataArticul={handleDataArticul}></MoviesAction> : false}
        {location === "#Animation" ? <MoviesAnimation handleDataArticul={handleDataArticul}></MoviesAnimation> : false}
        {location === "#Comedy" ? <MoviesComedy handleDataArticul={handleDataArticul}></MoviesComedy> : false}
        {location === "#ScienceFiction" ? <MoviesCF handleDataArticul={handleDataArticul}></MoviesCF> : false}
        {location.includes("Movie=") ? <LeerArticulo DataArticul={ReadArticul}></LeerArticulo> : false}
        {/* {location.includes(`Search=`) & nuevaBusqueda===true ? <MoviesSearch handleDataArticul={handleDataArticul}></MoviesSearch> : false} */}
        <Footer></Footer>
    </div>
);
}

export default Aplication;
