import React, { useState, useEffect } from 'react';
import Loader from './Loader';
import Star from "../Icons/star.svg";
import money from "../Icons/money.svg";
import clock from "../Icons/clock.svg";
import opinion from "../Icons/opinion.svg";


function LeerArticulo() {
    const [Noticia, setNoticias] = useState({});

    let rutaHast = window.location.hash,
        iniciaID = rutaHast.indexOf("="),
        IDCompleto = rutaHast.slice((iniciaID+1), rutaHast.length);

    useEffect(() => {
        const getNoticias = async (url) => {
            
            let res = await fetch(url),
                json = await res.json();
            console.log(json);

            let Pelicula = {

                name: json.title,
                Poster: `https://image.tmdb.org/t/p/original${json.backdrop_path}`,
                image: `https://image.tmdb.org/t/p/original${json.poster_path}`,
                description:json.overview,
                Puntuacion:json.vote_average,
                duracion: json.runtime,
                presupuesto: json.budget,
                Recaudacion: json.revenue,
                FechaLanzamiento: json.release_date,
                PaginaOficial: json.homepage,
                Votos: json.vote_count,
                Generos: json.genres,
                Productoras: json.production_companies,
            }

            setNoticias(Pelicula);

        }

        getNoticias(`https://api.themoviedb.org/3/movie/${IDCompleto}?api_key=30489992cf208d4c62469ce690c01f88&language=es`);
    }, []);

    return (
        <>
            {
                Noticia.Productoras === undefined ? <Loader></Loader> :
                <div className="ReadMovie" key={IDCompleto}>
                    <img src={Noticia.Poster} alt={Noticia.name} className="BannerBackground"></img>
                
                        <div className="PosterAndStars">
                            <div>
                            <img src={Noticia.image} alt={Noticia.name}></img>
                            </div>
                            <div>
                            <h3>Puntuación: <img src={Star} alt="Puntuación"></img> {Noticia.Puntuacion}/10</h3>
                            <h3>Duración: <img src={clock} alt="Duración"></img> {Noticia.duracion} m.</h3>
                            <h3>Presupuesto: <img src={money} alt="Presupuesto"></img> ${Noticia.presupuesto}</h3>
                            <h3>Votos: <img src={opinion} alt="Votos"></img> {Noticia.Votos}</h3>
                            </div>
                            

                    </div>
                    <div className="TitleAndDescription">
                        <h2 className="TitleArticuls">{Noticia.name}</h2>
                            <p>{Noticia.description}</p>
                            <div className="Generos">
                                <h4>GÉNEROS:</h4>
                                <div>
                                    {Noticia.Generos.map(el => (<button key={IDCompleto++}>{el.name}</button>))}
                                </div>
                            </div>
                            <h4>Fecha de estreno: {Noticia.FechaLanzamiento}</h4>
                            <h4>Pagina oficial: {Noticia.PaginaOficial} <a href={Noticia.PaginaOficial} target="_blank" rel="noreferrer">Visitar</a></h4>
                            <h4>Recaudación: ${Noticia.Recaudacion}</h4>
                            <div className="Generos Productoras">
                                <h4>Producido por:</h4>
                                <div>
                                    {Noticia.Productoras.map(el => (<button key={IDCompleto++}>{el.name}</button>))}
                                </div>
                            </div>

                            <h5>Datos obtenidos de www.themoviedb.org</h5>
                    </div>
                    <div className="ClaseDegradados">
                    </div>
                </div>
                
            }
            
        </>
        
    )
}

export default LeerArticulo;
